@import "styles/colors";

.App {
  font-family: 'Roboto', sans-serif;
  background-color: $mainColor;
  color: $textColor;

  a {
    text-decoration: none;
    color: $textColor;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 80px;
  height: 50px;

  .logo {
    //width: 50px;
    //height: 50px;
    //background-color: red;

    img {
      height: 60px;
    }
  }

  .nav-links {
    ul {
      list-style-type: none;
      display: flex;
      margin: 0;
      padding: 0;

      li {
        padding: 0 10px;

        a {
          font-size: 18px;
          display: flex;
          align-items: center;

          img {
            width: 20px;
            margin-right: 10px;
          }
          span {
            color: $red;
          }

        }
      }
    }
  }

  .tel {
    font-size: 20px;

    span {
      color: $red;
    }
  }
}

.container {
  padding: 0 80px;
  height: calc(100vh - 70px);

  .section-title {
    font-size: 40px;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 40px;

    span {
      color: $red;
    }
  }

  .info-rows {
    padding-bottom: 20px;

    .row {
      display: flex;
      border-bottom: 2px solid $border;
      padding: 20px 0;

      &:first-child {
        border-top: 2px solid $border;
      }

      &:hover {
        cursor: pointer;
        opacity: .5;
        transition: .3s;
      }

      .row-logo {
        display: flex;
        align-items: center;
        flex: 1;

        img {
          width: 55px;
          height: 60px;
        }
      }

      .row-question {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .answer {
          max-height: 0;
          -webkit-transition: max-height .4s;
          -moz-transition: max-height .4s;
          -ms-transition: max-height .4s;
          -o-transition: max-height .4s;
          transition: max-height .4s;
          overflow: hidden;
          margin: 0;
        }

        .active {
          max-height: 115px;
          margin: 10px 0;
        }
      }
    }
  }

  .main-page {
    display: flex;
    height: calc(100% - 170px);

    .img {
      display: flex;
      align-items: center;
      width: 750px;

      img {
        width: 100%;
      }
    }

    .title {
      flex: 1;
      display: flex;
      justify-content: center;
      flex-direction: column;

      .title-text {
        font-size: 80px;
        font-weight: bold;
        margin-bottom: 30px;
        text-transform: uppercase;
      }

      .description {
        display: flex;
        align-items: center;
        font-size: 24px;
        text-transform: uppercase;
        font-weight: 400;

        span {
          display: block;
          width: 10px;
          height: 10px;
          background-color: $red;
          border-radius: 50%;
          margin: 0 10px;
        }
      }

      .description-text {
        font-size: 14px;
        margin: 20px 0 50px;
        line-height: 24px;
      }

      .title-btn {
        display: flex;

        .call-btn:first-child {
          margin-right: 10px;
        }
      }
    }

    .call-btn {
      width: fit-content;
      background-color: $red;
      border: none;
      padding: 10px 40px;
      border-radius: 2px;
      font-size: 16px;
      font-weight: 600;
      text-transform: uppercase;
    }
  }

  .main-page-footer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .main-page-footer-link {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 1;
      text-transform: uppercase;
      text-align: center;
      min-width: 200px;

      .img {
        width: 100%;
        border-right: 1px solid #404040;
        border-top: 1px solid #404040;
        display: flex;
        justify-content: center;

        img {
          height: 100px;
          padding: 10px 0;
        }
      }

      h4 {
        margin: 10px 40px;
      }

      &:last-child {
        .img {
          border-right: none;
        }
      }
      &:after {

      }
    }
  }

  .why-choose-us {
    margin: 300px 0 200px;

    .row {
      //margin: 300px 0;
      text-transform: uppercase;

      .title {
        margin-bottom: 30px;

        h1 {
          margin: 5px 0;
          font-size: 40px;
          font-weight: 500;

          span {
            color: $red;
            margin-left: 10px;
          }
        }

        &-sub {
          color: $red;
        }
      }

      .qualities {
        display: flex;
        padding: 40px 0 60px;
        border-top: 1px solid $border;
        border-bottom: 1px solid $border;

        &-item {
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          flex: 1;
          color: $red;
          font-size: 120px;
          font-weight: 500;
          position: relative;
          text-align: center;

          span {
            color: #ffffff;
            display: block;
            font-size: 18px;
          }

          &:before {
            content: "";
            position: absolute;
            top: -40px;
            left: 50%;
            width: 1px;
            height: 35px;
            background-color: $border;
          }

          &:after {
            content: "";
            position: absolute;
            bottom: -60px;
            left: 50%;
            width: 1px;
            height: 35px;
            background-color: $border;
          }
        }
      }
    }

    .row-2 {
      margin-top: 150px;
      text-transform: uppercase;
      display: flex;

      &-block {
        flex: 1;
        text-align: right;
        display: flex;
        flex-direction: column;
        justify-content: center;

        h1 {
          margin: 5px 0;
          font-size: 40px;
          font-weight: 500;

          span {
            color: $red;
            margin-left: 10px;
          }
        }

        .title-sub {
          margin-top: 26px;
          font-size: 26px;
          font-weight: 500;
        }

        .call-me {
          flex: 1;
          text-align: left;
          margin-left: 30px;
          display: flex;
          align-items: center;

          img {
            height: 100px;
            width: 80px;
            margin-right: 20px;
          }

          a {
            font-size: 40px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .container {
    .main-page {
      .img {
        width: 600px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .container {
    .main-page {
      height: calc(100% - 179px);

      .img {
        width: 500px;
      }

      .title {
        .title-text {
          font-size: 40px;
        }

        .description {
          flex-wrap: wrap;
        }

        .description-text {
          flex-wrap: wrap;
        }
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .container {
    height: 100%;
    padding: 0 40px;

    .info-rows .row {
      .row-logo {
        flex: 0 0 80px;
      }

      .row-question .active {
        max-height: 77px;
      }
    }

    .section-title {
      font-size: 24px;
    }

    .main-page {
      flex-wrap: wrap;
      margin: 100px 0;

      .img {
        width: 400px;
      }

      .title {
        .title-text {
          font-size: 20px;
          margin-bottom: 10px;
        }

        .description {
          flex-wrap: wrap;
          font-size: 14px;
        }

        .description-text {
          flex-wrap: wrap;
          font-size: 12px;
          line-height: 14px;
          margin: 10px 0 20px;
        }

        .call-btn {
          font-size: 16px;
          padding: 5px 10px;
          font-weight: 600;
        }
      }
    }

    .main-page-footer {
      padding-bottom: 20px;

      .main-page-footer-link {
        border: 1px solid #404040;

        .img {
          border-top: none;
          border-right: none;
        }
      }
    }

    .why-choose-us {
      padding: 300px 0 200px;
      margin: 0;

      .row {
        .title {
          h1 {
            font-size: 24px;
          }
        }

        .qualities .qualities-item {
          font-size: 60px;

          span {
            font-size: 12px;
          }
        }
      }

      .row-2 {
        flex-wrap: wrap;

        .row-2-block {
          h1 {
            font-size: 24px;
          }

          .title-sub {
            font-size: 20px;
            margin-top: 10px;
          }

          .call-me {
            min-width: 264px;
            margin-top: 20px;

            img {
              height: 40px;
              width: 40px;
            }

            a {
              font-size: 24px;
              width: 204px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 760px) {
  .header {
    padding: 10px 40px;

    .nav-links ul li a {
      font-size: 16px;
    }
  }

  .container {
    .info-rows .row .row-question .active {
      max-height: 96px;
    }

    .main-page {
      flex-wrap: wrap;
      justify-content: center;
      margin: 25px 0 100px;

      .img {
        width: 400px;
        height: 270px;
      }

      .title {
        min-width: 200px;
        align-items: center;
        padding: 0 40px;

        .title-text,
        .description,
        .description-text {
          justify-content: center;
          text-align: center;
        }
      }
    }
  }
}

@media screen and (max-width: 580px) {
  .header .nav-links ul {
    display: flex;
    flex-direction: column;

    li a {
      margin: 5px 0;
      font-size: 14px;
    }
  }

  .container {
    .main-page .title {
      padding: 0;
    }

    .info-rows .row .row-question .active {
      max-height: 134px;
    }

    .section-title {
      font-size: 24px;
      text-align: center;
    }

    .why-choose-us {
      .row .qualities {
        padding: 0;
        border-top: none;
        border-bottom: none;
        flex-wrap: wrap;

        .qualities-item {
          position: inherit;
          border: 1px solid #404040;
          padding: 20px 10px;
          margin: 5px;
          min-width: 120px;

          &:before, &:after {
            display: none;
          }
        }
      }

      .row-2 {
        .row-2-block {
          align-items: center;
          text-align: center;

          .call-me {
            margin-left: 0;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .header {
    padding: 10px 20px;

    .tel {
      font-size: 14px;
    }
  }

  .container {
    padding: 0 40px;

    .main-page {
      .img {
        width: 360px;
        height: 230px;
      }

      .title .title-btn {
        flex-direction: column;
        align-items: center;

        .call-btn {
          padding: 15px 15px;
          width: 230px;

          &:first-child {
            margin-right: 0;
            margin-bottom: 10px;
          }
        }
      }
    }

    .info-rows .row {
      .row-logo {
        flex: 0 0 50px;

        img {
          width: 40px;
          height: 45px;
        }
      }

      .row-question {
        .active {
          max-height: 210px;
          font-size: 14px;
        }

        .wrap-img img {
          width: 40px;
          height: 40px;
        }
      }
    }

    .why-choose-us .row .title h1 {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 320px) {
  .header {
    padding: 10px 10px;

    .tel {
      font-size: 12px;
    }
  }

  .container {
    padding: 0 10px;

    .info-rows .row .row-question .active {
      max-height: 202px;
      font-size: 12px;
    }
  }
}